.couple-section {
	position: relative;
	text-align: center;
	.and {
		@include min-md {
			position: absolute;
			top: 80%;
			left: 50%;
			transform: translate(-50%, -26%);
		}
		font-weight: 600;
		display: flex;
		justify-content: center;
		align-items: center;
		width: fit-content;
		max-width: 100%;
		margin: 1rem auto;
		padding-left: 1rem;
		position: relative;
		z-index: 3;
		mask-image: url(../images/mask.png);
		mask-size: 100% 100%;
		mask-repeat: no-repeat;
		&::before {
			position: absolute;
			top: 0;
			right: 0;
			bottom: 0;
			left: 0;
			background-color: var(--color-main);
			opacity: 0.7;
			content: "";
		}
		span {
			position: relative;
			font-size: 48px;
			font-family: $font-secondary;
			color: var(--color-primary);
		}
	}
	.image-couple-container {
		position: relative;
		width: 100%;
		padding: 3rem 0;
		.image-wrapper {
			position: relative;
			width: 100%;
			aspect-ratio: 7/8;
			background-color: var(--color-primary);
			padding: 1rem;
			.couple-background {
				width: 100%;
				height: 100%;
				object-fit: cover;
			}
		}
	}
	.couple-wrapper {
		position: relative;
		padding: 5rem 0 0;
		@include max-md {
			padding: 3rem 0 0;
		}
		.side-1 {
			position: absolute;
			top: 0;
			left: 0;
			width: 140px;
		}
		.side-2 {
			position: absolute;
			top: 0;
			right: 0;
			width: 140px;
			transform: scaleX(-1);
		}
		.orn-1 {
			position: absolute;
			top: 0;
			left: 0;
			width: 50px;
		}
		.orn-2 {
			position: absolute;
			top: 0;
			right: 0;
			width: 55px;
		}
		.orn-3 {
			position: absolute;
			top: 0;
			right: 0;
			width: 50px;
			transform: scaleX(-1);
		}
		.orn-4 {
			position: absolute;
			top: 0;
			left: 0;
			width: 55px;
			transform: scaleX(-1);
		}
	}
	.couple-body {
		position: relative;
		display: flex;
		justify-content: center;
		background-color: var(--color-primary);
		color: var(--color-light);
	}
	.couple-content {
		position: relative;
		margin-bottom: 3rem;
	}
	.couple {
		position: relative;
		text-align: center;
		padding: 3rem 0 0;
		.image-wrap {
			width: calc(100% - 13rem);
			position: relative;
			aspect-ratio: 7/8;
			margin: 0 auto;
			.image-element {
				position: relative;
				width: 100%;
				height: 100%;
				.couple-image {
					height: 100%;
					object-fit: cover;
					position: relative;
					width: 100%;
					border-radius: 4px;
					border: 2px solid #fff;
				}
			}
		}
		.couple-description {
			position: relative;
			background-color: var(--color-primary);
			width: calc(100% - 30px);
			overflow: hidden;
			&-body {
				position: relative;
				padding: 2rem 8rem 3rem;
			}
		}
		.couple-parent {
			width: 100%;
		}
		.sosmed-wrap {
			position: relative;
			width: fit-content;
			max-width: 100%;
			background: var(--color-secondary);
			padding: 0.5rem 0.6rem;
			border-radius: 50px;
			.sosmed {
				color: var(--color-light);
				text-decoration: none;
				display: block;
				width: fit-content;
				margin: 0 auto;
				small {
					align-items: center;
					gap: 0.25rem;
					display: flex;
					justify-content: center;
				}
			}
			.fab {
				font-size: 18px;
				@include max-md {
					font-size: 16px;
				}
			}
		}
		&.man {
			.couple-description {
				text-align: left;
				border-top-right-radius: 100px;
				.orn-elephent {
					position: absolute;
					top: 0;
					left: 0;
					width: 80%;
				}
			}
			.sosmed-wrap {
				margin-right: auto;
			}
		}
		&.woman {
			.couple-description {
				text-align: right;
				border-top-left-radius: 100px;
				margin-left: auto;
				.orn-elephent {
					position: absolute;
					top: 0;
					right: 0;
					width: 80%;
					transform: scaleX(-1);
				}
			}
			.sosmed-wrap {
				margin-left: auto;
			}
		}
	}
}
