.thank-section {
	position: relative;
	padding: 5rem 0;
	text-align: center;
	@include max-md {
		padding: 3rem 0;
	}
	.image-wrapper {
		position: relative;
		width: 90%;
		height: auto;
		aspect-ratio: 7/8;
		margin: 0 auto;
		.image-element {
			position: relative;
			width: 100%;
			height: 100%;
			.thank-background {
				width: 100%;
				height: 100%;
				object-fit: cover;
				border-radius: 4px;
				border: 1rem solid var(--color-primary);
			}
		}
	}
	.thank-wrapper {
		position: relative;
		.thank {
			position: relative;
			mask-image: url(../images/mask.png);
			mask-size: 100% 100%;
			mask-repeat: no-repeat;
			color: var(--color-light);
			&::before {
				position: absolute;
				top: 0;
				right: 0;
				bottom: 0;
				left: 0;
				background-color: var(--color-primary);
				opacity: 0.7;
				content: "";
			}
			&-body {
				position: relative;
				padding: 10rem 2rem;
				@include max-md {
					padding: 8rem 1rem;
				}
			}
		}
	}
}
.footer {
	padding: 1rem 0;
	position: relative;
	text-align: center;
	background-color: var(--color-secondary);
	color: var(--text-button);
	small {
		position: relative;
	}
}
