.rsvp-section {
	position: relative;
	padding: 5rem 0 0;
	@include max-md {
		padding: 3rem 0 0;
	}
	.rsvp-wrapper {
		position: relative;
		padding: 10rem 2rem 5rem;
		color: var(--color-light);
		@include max-md {
			padding: 8rem 1rem 3rem;
		}
		&::before {
			position: absolute;
			top: 0;
			right: 0;
			bottom: 0;
			left: 0;
			background-color: var(--color-primary);
			opacity: 0.7;
			border-radius: 300px 300px 0 0;
			content: "";
		}
		.rsvp-form {
			position: relative;
			.title-section {
				&::before {
					all: unset;
				}
			}
		}
	}
}
