.venue-section {
	position: relative;
	padding: 5rem 0;
	overflow: hidden;
	@include max-md {
		padding: 3rem 0;
	}
	.side-1 {
		position: absolute;
		top: 0;
		left: 0;
		width: 120px;
	}
	.side-2 {
		position: absolute;
		top: 0;
		right: 0;
		width: 120px;
		transform: scaleX(-1);
	}
	.side-3 {
		position: absolute;
		top: 0;
		left: 0;
		width: 35px;
	}
	.side-4 {
		position: absolute;
		top: 0;
		right: 0;
		width: 35px;
		transform: scaleX(-1);
	}
	.venue-wrapper {
		position: relative;
		z-index: 3;
		.venue-description {
			position: relative;
			margin: 0 auto 5rem;
			z-index: 2;
			text-align: center;
			@include max-lg {
				margin-bottom: 3rem;
			}
		}
		.venue-content {
			position: relative;
			.ribbon-venue {
				position: relative;
				svg {
					width: 40px;
					height: auto;
					path {
						stroke: var(--color-dark);
					}
				}
				.st0 {
					fill: var(--color-secondary);
				}
				.st1 {
					fill: none;
					stroke: var(--color-secondary);
					stroke-width: 19;
					stroke-linecap: round;
					stroke-miterlimit: 10;
					transform-origin: 50% 75%;
				}
			}
			.card {
				background-color: transparent;
				z-index: 5;
				text-align: center;
				border: none;
				.card-body {
					position: relative;
					padding: 0;
					h4 {
						display: block;
						width: 100%;
						padding: 1rem 0;
						text-rendering: optimizeLegibility;
						-webkit-font-feature-settings: "kern" 1;
						font-feature-settings: "kern" 1;
					}
					.event-description {
						position: relative;
						padding: 2rem 0;
						color: var(--color-light);
					}
				}
			}
		}
	}
	.prokes-wrapper {
		margin: 2rem 0 0;
		position: relative;
		text-align: center;
		z-index: 2;
		.title-section {
			&::before {
				all: unset;
			}
		}
		.prokes-wrap {
			display: flex;
			gap: 0.75rem;
			justify-content: center;
			align-items: flex-start;
			flex-wrap: wrap;
			text-align: center;
			.prokes {
				display: flex;
				flex-direction: column;
				align-items: center;
				justify-content: flex-start;
				width: 132px;
				@include max-md {
					width: 120px;
				}
				@include max-xs {
					width: 45%;
				}
			}
		}
	}
	.row-venue {
		row-gap: 3rem;
	}
	.event-name {
		position: relative;
		width: 100%;
		background-color: var(--color-primary);
		color: var(--color-light);
		padding: 5rem 1rem;
		.orn-2 {
			position: absolute;
			top: 0;
			left: 0;
			width: 100%;
		}
		.orn-1 {
			position: absolute;
			bottom: 0;
			left: 0;
			width: 100%;
		}
		&-body {
			position: relative;
		}
		.date {
			position: relative;
			width: 100%;
			display: flex;
			flex-direction: column;
			justify-content: center;
			align-items: center;
			span {
				font-family: $font-secondary;
				font-size: 32px;
			}
		}
		h4 {
			font-family: $font-secondary;
		}
		p {
			margin-bottom: 0;
			font-weight: 600;
		}
	}
	.event-place {
		position: relative;
		padding: 0 3rem 3rem;
		&-body {
			position: relative;
			padding: 2rem;
			background-color: var(--color-main);
			color: var(--color-dark);
			border-radius: 45px 45px 10px 10px;
		}
	}
	.animation-loop {
		.st1 {
			will-change: transform;
			animation: zoomInRing 2000ms ease-out infinite;
		}
		.dot {
			will-change: transform;
			animation: fadeBoing 2000ms ease-out infinite;
			transform-origin: 50% 75%;
		}
		#Layer_3 {
			will-change: transform;
			animation: boing 1000ms ease-out infinite alternate;
		}
	}

	@keyframes zoomInRing {
		0% {
			opacity: 0;
			transform: scale3d(0.5, 0.5, 0.5);
		}
		40% {
			opacity: 1;
		}
		70% {
			opacity: 0;
			transform: scale3d(1.25, 1.25, 1.25);
		}
		100% {
			opacity: 0;
		}
	}

	@keyframes boing {
		0% {
			transform: translate3d(0, 0, 0);
		}
		100% {
			transform: translate3d(0, -5rem, 0);
		}
	}

	@keyframes fadeBoing {
		0% {
			opacity: 1;
		}
		50% {
			transform: scale3d(0.25, 0.25, 0.25);
			opacity: 0;
		}
		100% {
			transform: scale3d(1, 1, 1);
			opacity: 1;
		}
	}
}

.maps-embed {
	width: 100%;
	height: 100%;
	position: absolute;
	top: 0;
	left: 0;
}
