.apology-section {
	position: relative;
	padding: 5rem 0;
	@include max-md {
		padding: 3rem 0;
	}
	.appology-wrap {
		position: relative;
		padding: 10rem 2rem;
		color: var(--color-light);
		&::before {
			position: absolute;
			top: 0;
			right: 0;
			bottom: 0;
			left: 0;
			background-color: var(--color-primary);
			opacity: 0.7;
			border-radius: 300px;
			content: "";
		}
		@include max-md {
			padding: 8rem 0;
		}
	}
}
