.cover-section {
	position: fixed;
	z-index: 1000;
	transition: all 1s ease-out;
	background-color: var(--color-primary);
	color: var(--color-light);
	overflow: hidden;
	width: 100%;
	&.cover-opened {
		transform: translateY(-100%);
	}
	.orn-99 {
		position: relative;
		width: 180px;
		margin: 0 auto 1.5rem;
	}
	.orn-98 {
		position: absolute;
		top: 50%;
		left: 50%;
		width: 180%;
		transform: translate(-50%, -50%);
	}
	.orn-1 {
		position: absolute;
		left: 0;
		top: 0;
		width: 50px;
	}
	.orn-2 {
		position: absolute;
		top: 0;
		right: 0;
		width: 50px;
		transform: scaleX(-1);
	}
	.cover-wrapper {
		position: relative;
		display: flex;
		justify-content: center;
		align-items: flex-start;
		padding: 4rem 0 3rem;
		transition: opacity 1s ease;
		height: 100vh;
		@include max-lg {
			padding: 4rem 0 8rem;
			align-items: center;
		}
		.cover-content {
			text-align: center;
			position: relative;
			background-color: transparent;
			border-radius: 12px;
			width: 100%;
			height: 100%;
			display: flex;
			align-items: center;
			justify-content: center;
			.cover-body {
				position: relative;
				display: flex;
				align-items: center;
				justify-content: center;
				padding: 1rem 0;
				.greeting-wrapper {
					position: relative;
					h6 {
						font-weight: 700;
					}
				}
				h6 {
					font-weight: 600;
				}
				.cover-description {
					padding: 0 2rem;
					text-align: center;
					@include max-lg {
						padding: 2rem 0 1rem;
					}
					.cover-description {
						position: relative;
						z-index: 5;
					}
					.name {
						position: relative;
						width: 100%;
						.title {
							position: relative;
							line-height: 1.2;
							color: var(--color-gold);
							padding: 1rem 0;
						}
					}
					.cover-event {
						position: relative;
						z-index: 5;
					}
				}
			}
			.elements-widget {
				width: 100%;
			}
		}
	}
}
