.reminder-wrap {
	position: relative;
	display: block;
	padding: 5rem 0;
	@include max-md {
		padding: 3rem 0;
	}
	.orn-1 {
		position: absolute;
		top: 0;
		left: 0;
		width: 100%;
	}
	.orn-2 {
		position: absolute;
		bottom: 0;
		left: 0;
		width: 100%;
		transform: scaleY(-1);
	}
	.orn-3 {
		position: absolute;
		top: 50%;
		left: 50%;
		width: 90%;
		transform: translate(-50%, -30%);
	}
	.reminder-content {
		position: relative;
		padding: 5rem 0 8rem;
		width: 100%;
		z-index: 2;
	}
	.btn-reminder {
		z-index: 3;
		position: relative;
		margin: 0 auto;
		display: flex;
		align-items: center;
		position: relative;
		gap: 0.5rem;
		transition: all 0.2s ease-in-out;
		text-decoration: none;
		margin-top: 1.5rem;
		path {
			fill: var(--text-button);
			transition: all 0.3s ease-in-out;
		}
	}
}
