.wishes-section {
	position: relative;
	.side-1 {
		position: absolute;
		top: 0;
		left: 0;
		width: 120px;
	}
	.side-2 {
		position: absolute;
		top: 0;
		right: 0;
		width: 120px;
		transform: scaleX(-1);
	}
	.side-3 {
		position: absolute;
		top: 0;
		left: 0;
		width: 100%;
	}
	.side-4 {
		position: absolute;
		bottom: 0;
		left: 0;
		width: 100%;
		transform: scaleY(-1);
	}
	.image-wrapper {
		position: relative;
		width: 100%;
		padding: 10rem 0;
		background-color: var(--color-primary);
		@include max-md {
			padding: 8rem 0;
		}
		.image-element {
			aspect-ratio: 7/8;
			.wishes-background {
				width: 100%;
				height: 100%;
				object-fit: cover;
				border-radius: 4px;
				border: 2px solid #fff;
			}
		}
	}
	.wishes-form-wrapper {
		position: relative;
		padding: 0 5rem;
		.orn-99 {
			position: relative;
			width: 100px;
			margin: 0 auto 1rem;
		}
	}
	.wishes-content {
		position: relative;
		display: flex;
		align-items: stretch;
		overflow: hidden;
	}
	.card-form {
		margin-bottom: 0;
		background-color: transparent;
		position: relative;
		z-index: 3;
		&.card {
			border: none;
		}
		.card-body {
			padding: 3rem 2rem;
			@include max-md {
				padding: 2rem 1rem;
			}
			.form-control {
				font-family: $font-primary;
				border-color: var(--color-dark);
				background-color: transparent;
				color: var(--color-dark);
				&::placeholder {
					color: var(--color-dark);
				}
				&:focus {
					box-shadow: none;
					background-color: transparent;
					color: var(--color-dark);
				}
			}
		}
	}

	.wishes-preview {
		position: relative;
		background-color: var(--color-primary);
		padding: 0 5rem;
		.card {
			position: relative;
			border: none;
			background-color: transparent;
			.card-body {
				overflow: hidden;
				padding: 3rem 2rem;
				.wishes {
					text-align: left;
					max-height: 32rem;
					overflow: auto;
					padding-right: 1rem;
					color: var(--color-dark);
					&::-webkit-scrollbar {
						width: 5px;
					}
					&::-webkit-scrollbar-track {
						background-color: #eee;
						border-radius: 20px;
						opacity: 0.15;
					}
					&::-webkit-scrollbar-thumb {
						background-color: var(--color-secondary);
						border-radius: 20px;
					}
					// .text-muted {
					// 	color: rgba($color: #fff, $alpha: 0.8) !important;
					// }
					.wish {
						margin-bottom: 1.5rem;
						display: grid;
						grid-template-columns: auto 1fr;
						gap: 1rem;
						h6 {
							font-weight: bold;
						}
						h6,
						p {
							margin-bottom: 0;
						}
						.wish-badge {
							background-color: var(--color-light);
							width: 35px;
							aspect-ratio: 1;
							display: flex;
							justify-content: center;
							align-items: center;
							border-radius: 50px;
						}
						.wish-description {
							padding: 1rem;
							background-color: rgba($color: #fff, $alpha: 0.8);
							border-radius: 5px;
							position: relative;
							&::before {
								position: absolute;
								top: 15px;
								left: 0;
								width: 15px;
								height: 15px;
								transform: translateX(-100%);
								clip-path: url(#wishes-polygon);
								content: "";
								background-color: rgba($color: #fff, $alpha: 0.8);
							}
						}
					}
				}
			}
		}
	}
}
